import { mapMutations } from 'vuex';

export default{
	data(){
		return {
			mobile: '',
			password: '',
			logining: false
		}
	},
	onLoad(){
		
	},
	methods: {
		...mapMutations(['login','setToken']),
		inputChange(e){
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack(){
			uni.navigateBack();
		},
		async toLogin(){
			this.logining = true;
			const {mobile, password} = this;
			this.$http.post('/cms/register/get_token', {user_name:mobile,passwd:password}).then(res => {
				if(res.data.token){
					this.setToken(res.data.token);
					uni.navigateBack();
					uni.$emit('info',{msg:''})
				}else{
					this.$api.msg(res.info.status_err);
					this.logining = false;
				}
			}).catch(err => {});
		}
	},

}
